<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="編輯" icon="mdi-pencil" size="small" variant="text"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card class="review-border">
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue">編輯人員</span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-text-field hide-details="auto" v-model="user.email" density="compact" label="帳號/信箱"
            prepend-inner-icon="mdi-email" variant="solo" required class="mb-5" readonly>
            <template #label>
              帳號/信箱
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="user.name" density="compact" label="使用者名稱"
            prepend-inner-icon="mdi-account" variant="solo" required class="mb-5" readonly>
            <template #label>
              使用者名稱
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="user.unit" density="compact" label="單位"
            prepend-inner-icon="mdi-domain" variant="solo" required class="mb-5" readonly>
            <template #label>
              單位
            </template>
          </v-text-field>
          <div class="mt-4">
            <strong>狀態:</strong>
            <v-btn class="review-button-edit mx-4" :color="form.review === '啟用' ? 'success' : 'error'" variant="tonal"
              @click="setReviewStatus()">
              <span v-if="form.review === '啟用'"><span class="review-button-content">啟用</span><span
                  class="review-button-circle">●</span></span>
              <span v-else><span class="review-button-circle">●</span><span
                  class="review-button-content">停用</span></span>
            </v-btn>
          </div>
          <v-select v-model="form.roles" :items="rolesList" item-title="name" item-value="id" label="角色"
            variant="underlined" chips multiple>
          </v-select>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
          <v-btn text="確認" variant="outlined" color="primary" @click="submitEdit()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
import { roles } from '@/requests';
import { users } from '@/requests';
const props = defineProps({
  user: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      email: "",
      name: "",
      phone: "",
      unit: "",
      createTime: "",
      review: "",
      reason: "",
      rejectReason: "",
      roles: []
    }),
  },
});

const rolesList = ref([]);
const form = ref({
  id: "",
  review: "",
  roles: []
})
onMounted(() => {
  form.value.id = props.user.id;
  form.value.review = props.user.review;
  form.value.roles = props.user.roles;
  roles.list().then((response) => {
    rolesList.value = response.data;
  });
});
const isActive = ref(false);
const emit = defineEmits(['edit-user']);
function submitEdit() {
  users.edit(form.value).then(() => {
    emit('edit-user', { ...form.value });
    isActive.value = false;
  }).catch((error) => {
    alert("編輯失敗，請重新整理後再試一次");
    isActive.value = false
  });
}
function cancele() {
  form.value.review = props.user.review;
  form.value.unit = props.user.unit;
  form.value.roles = props.user.roles;
  isActive.value = false
}
function setReviewStatus() {
  form.value.review = (form.value.review == "停用") ? "啟用" : "停用";
}
</script>
<style scoped>
.review-border {
  padding: 15px;
}

.review-button-edit {
  border-radius: 100px !important;
  /* height: 28px !important; */
  width: 60px !important;
}

.review-button-circle {
  font-size: 24px;
}

.review-button-content {
  font-size: 12px;
  position: relative;
  top: -5px;
}
</style>