<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="審核狀態" size="small" color="warning" text="審核"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card class="review-border">
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue">審核狀態</span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-text-field hide-details="auto" v-model="user.email" density="compact" label="帳號/信箱"
            prepend-inner-icon="mdi-email" variant="solo" required class="mb-5" readonly>
            <template #label>
              帳號/信箱
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="user.name" density="compact" label="使用者名稱"
            prepend-inner-icon="mdi-account" variant="solo" required class="mb-5" readonly>
            <template #label>
              使用者名稱
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="user.unit" density="compact" label="單位"
            prepend-inner-icon="mdi-domain" variant="solo" required class="mb-5" readonly>
            <template #label>
              單位
            </template>
          </v-text-field>
          <div><strong>申請原因:</strong> {{ user.reason }}</div>
          <div><strong>申請時間:</strong> {{ user.createTime }}</div>
          <div class="mt-4">
            <strong>審核狀態:</strong>
            <v-btn-toggle class="mx-4" v-model="form.review" mandatory>
              <v-btn class="review-button mx-4" value="啟用" color="success" variant="tonal">啟用</v-btn>
              <v-btn class="review-button mx-4" value="退件" color="error" variant="tonal">退件</v-btn>
            </v-btn-toggle>
          </div>
          <div v-if="form.review === '退件'" class="mt-4">
            <v-textarea v-model="form.rejectReason" label="退件原因" density="compact" variant="solo" required
              placeholder="請輸入退件原因"></v-textarea>
          </div>
          <div v-if="form.review === '啟用'">
            <v-select v-model="form.roles" :items="rolesList" item-title="name" item-value="id" label="角色"
              variant="underlined" chips multiple>
            </v-select>
          </div>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
          <v-btn text="確認" variant="outlined" color="primary" @click="submitReview()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
import { roles } from '@/requests';
import { users } from '@/requests';

const props = defineProps({
  user: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      email: "",
      name: "",
      phone: "",
      unit: "",
      createTime: "",
      review: "",
      reason: "",
      rejectReason: "",
      roles: []
    }),
  },
});

const rolesList = ref([]);
const form = ref({
  id: "",
  review: "",
  rejectReason: "",
  roles: []
});
onMounted(() => {
  form.value.id = props.user.id;
  form.value.review = props.user.review;
  form.value.rejectReason = props.user.rejectReason;
  form.value.roles = props.user.roles;
  roles.list().then((response) => {
    rolesList.value = response.data;
  });
});
const isActive = ref(false);
const emit = defineEmits(['review-user']);
function submitReview() {
  form.value.id = props.user.id;
  users.review(form.value).then(() => {
    emit('review-user', { ...form.value });
    isActive.value = false;
  }).catch((error) => {
    alert("審核失敗，請重新整理後再試一次");
  });
}
function cancele() {
  form.value.review = props.user.review;
  form.value.rejectReason = props.user.rejectReason;
  form.value.unit = props.user.unit;
  form.value.roles = props.user.roles;
  isActive.value = false
}
</script>
<style scoped>
.review-border {
  padding: 15px;
}

.review-button {
  border-radius: 100px !important;
  height: 36px !important;
}
</style>