<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="刪除" icon="mdi-trash-can" size="small" variant="text"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card>
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue"> </span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <div class="delete-info">
            <img src="@/assets/images/delete-alert-icon.png" alt="Delete Alert Icon" class="delete-alert" />
            <span>是否確認<span class="text-red">刪除</span>?</span>
            <!-- <v-text-field hide-details="auto" v-model="user.email" density="compact" label="帳號/信箱"
              prepend-inner-icon="mdi-email" variant="solo" required class="mb-5" readonly>
              <template #label>
                帳號/信箱
              </template>
            </v-text-field>
            <v-text-field hide-details="auto" v-model="user.name" density="compact" label="使用者名稱"
              prepend-inner-icon="mdi-account" variant="solo" required class="mb-5" readonly>
              <template #label>
                使用者名稱
              </template>
            </v-text-field> -->
          </div>
        </v-card-text>
        <v-card-actions class="d-flex justify-center mb-5">
          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
            <v-spacer style="flex: 0.25;"></v-spacer>
          <v-btn text="確認" variant="outlined" color="primary" @click="submitDelete()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
const props = defineProps({
  user: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      email: "",
      name: "",
      phone: "",
      unit: "",
      createTime: "",
      review: "",
      reason: "",
      rejectReason: "",
      roles: []
    }),
  },
});

const emit = defineEmits(['delete-user']);

const form = ref({
  id: "",
  review: "",
  unit: "",
  roles: []
})
onMounted(() => {
  form.value.review = props.user.review;
  form.value.unit = props.user.unit;
  form.value.roles = props.user.roles;
});
const isActive = ref(false);
function submitDelete() {
  // API 刪除使用者
  emit('delete-user', props.user.id);
  isActive.value = false
}
function cancele() {
  isActive.value = false
}
</script>
<style scoped>
.delete-info {
  font-size: 18px;
  margin: 0px 40px 0px 40px;
}
.delete-alert {
  vertical-align: middle;
  margin-right: 64px;
  width: 64px; 
  height: 64px;
}
</style>