<template>
  <v-row class="mt-1">
    <v-col cols="4">
      <v-select label="角色篩選" v-model="selectedRole" :items="rolesList" variant="outlined" density="compact"
        clearable></v-select>
    </v-col>
    <v-spacer></v-spacer>
    <v-col cols="4">
      <v-text-field label="搜尋" v-model="filterWords" density="compact" variant="outlined"></v-text-field>
    </v-col>
  </v-row>
  <v-card class="rounded-lg elevation-3">
    <v-data-table :headers="headers" :items="filterRows" density="comfortable" class="cyan-header">
      <template v-slot:item.role="{ item }">
        <v-chip v-for="role in item.roles" :key="role" class="ma-1" color="primary" text-color="white" size="small">
          {{ role.name }}
        </v-chip>
      </template>
      <template v-slot:item.review="{ item }">
        <template v-if="item.review === '待審核'">
          <Review :user="item" @review-user="onUserReview"></Review>
        </template>
        <template v-else-if="item.review === '退件'">
          <span style="color: red;">{{ item.review }}</span>
        </template>
        <template v-else-if="item.review === '啟用'">
          <span style="color: green;">{{ item.review }}</span>
        </template>
        <template v-else-if="item.review === '停用'">
          <span style="color: gray;">{{ item.review }}</span>
        </template>
        <template v-else>
          <span>{{ item.review }}</span>
        </template>
      </template>
      <template v-slot:item.edit="{ item }">
        <Edit v-if="item.review !== '待審核'" :user="item" @edit-user="onUserEdit"></Edit>
      </template>
      <template v-slot:item.delete="{ item }">
        <Delete :user="item" @delete-user="onUserDeleted"></Delete>
      </template>
    </v-data-table>
  </v-card>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import axios from "@/axios";
import * as ss from "@/helpers/sessionStorage";
import Review from "./review.vue";
import Edit from "./edit.vue";
import Delete from "./delete.vue";
import { users } from "@/requests";

const response = ref([]);
onMounted(() => {
  refreshList();
});

const filterWords = ref("");
const selectedRole = ref("");
const filterRows = computed(() => {
  return response.value
    .filter((item) => {
      const matchesFilterWords =
        !filterWords.value ||
        (item.name && item.name.includes(filterWords.value)) ||
        (item.userName && item.userName.includes(filterWords.value)) ||
        (item.email && item.email.includes(filterWords.value));
      const matchesSelectedRole =
        !selectedRole.value || (item.roles && item.roles.includes(selectedRole.value));
      return matchesFilterWords && matchesSelectedRole;
    })
    .sort((a, b) => {
      if (a.review === '待審核' && b.review !== '待審核') return -1;
      if (a.review !== '待審核' && b.review === '待審核') return 1;
      return new Date(b.createTime) - new Date(a.createTime);
    });
});

const headers = ref([
  { title: "使用者", key: "name" },
  { title: "帳號/E-mail", key: "email" },
  { title: "角色", key: "role" },
  { title: "單位", key: "unit" },
  { title: "狀態", key: "review", sortable: false },
  { title: "編輯", key: "edit", sortable: false },
  { title: "刪除", key: "delete", sortable: false },
]);
const rolesList = ref([
  "一般使用者",
  "管理員",
  "測試人員",
  "師父",
  "徒弟"
]);
const selected = ref()
const model = ref({
  name: "",
  email: "",
  unit: "",
  phone: "",
  reason: ""
})
function onUserReview() {
  refreshList();
}
function onUserEdit() {
  refreshList();
}
function onUserDeleted() {
  refreshList();
}
function refreshList() {
  users.list().then((a) => {
    response.value = a.data;
  });
}
</script>
<style>
.cyan-header thead th {
  color: #3B9AB8;
  font-weight: bold !important;
}
</style>
